<template lang="pug">
    div
        admin-header
        b-container
            b-row.categories-panel
                b-col
                    b-row
                        b-form(@submit.prevent="submitCat" class="w-100")
                            b-form-group(
                                        label="Добавить рубрику:"
                                        label-for="categoryName"
                                        description="Введите название рубрики")
                                b-form-input(
                                        id="categoryName"
                                        type="text"
                                        v-model="categoryName"
                                        required
                                        placeholder="Название рубрики")
                                b-alert(
                                        variant="danger"
                                        :show="!!alertCategory"
                                        v-show="!!alertCategory") {{ alertCategory }}
                            b-button(type="submit") Добавить
                    b-row
                        b-form(@submit.prevent="submitSubCat" v-if="categories" class="w-100")
                            b-form-group(
                                        label="Добавить подрубрику:"
                                        label-for="subCategoryName"
                                        description="Добавьте подрубрику")
                                b-form-select(
                                    v-model="selectedCategory"
                                    :options="optionsCategories")
                                b-form-input(
                                        v-if="selectedCategory"
                                        id="subCategoryName"
                                        type="text"
                                        v-model="subcategoryName"
                                        required
                                        placeholder="Название подрубрики")
                            b-button(type="submit") Добавить
                categories-list(:categories="categories")
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
import cyrToLat from '../../jsModules/cyrToLat';
export default {
    data() {
        return {
            categoryName: '',
            subcategoryName: '',
            selectedCategory: '',
            alertCategory: '',
        };
    },
    computed: {
        ...mapGetters('СategoriesStore', ['categories']),
        optionsCategories() {
            let resultOptions = [];
            if (this.categories) {
                this.categories.map(category => {
                    resultOptions.push(category.title);
                });
            }
            return resultOptions;
        }
    },
    methods: {
        ...mapActions('СategoriesStore',
            ['getAllCategories', 'addNewCategory', 'editCategory']),
        submitCat() {
            // this func preparing and send new category to server
            const postNewCategory = () => {
                // create new category obj
                const newCategory = {
                    title: this.categoryName,
                    link: cyrToLat(this.categoryName),
                    subcategories: [],
                };
                // post new category and refresh
                this.addNewCategory({ ...newCategory })
                    .then(response => {
                        this.getAllCategories();
                    });
            }
            // check - are this category exist
            let exist = false;
            this.categories.map(category => {
                if (category.title === this.categoryName) {
                    exist = true; return;
                }
            });
            // add if this is realy new category
            if (!exist) {
                this.alertCategory = '';
                postNewCategory();
            } else {
                this.alertCategory = 'Такая рубрика уже существует!';
            }
            this.categoryName = '';
        },
        submitSubCat() {
            // this func preparing and send new subcategory to server
            const updatingCategory = this.categories.find(category => {
                    return category.title === this.selectedCategory;
                });
            const newSubcategory = {
                title: this.subcategoryName,
                link: cyrToLat(this.subcategoryName),
            };
            updatingCategory.subcategories.push(newSubcategory);
            this.selectedCategory = '';
            this.subcategoryName = '';
            this.editCategory(updatingCategory)
            .then(response => {
                this.getAllCategories();
            })
            .catch(e => console.error(e));
        },
    },
    components: {
        categoriesList: require('./categoriesList'),
        AdminHeader: require('./../_admin-common/header'),
    }
}
</script>

<style lang="sass" scoped>
.categories-panel
    margin-top: 40px
</style>



