<template lang="pug">
    main.content
        .last-news
            section(v-for="postCat in categories").last-news__list
                div(v-if="postsByCategory(postCat.title).length").last-news__category
                    .last-news__top
                        h2.title.last-news__title {{ postCat.title }}
                        router-link( :to="postCat.link").link Перейти в рубрику
                    ul.last-news-posts__list
                        router-link(
                                v-for="post in postsByCategory(postCat.title)"
                                :to=`post.categories.link + '/' + (post.categories.subcategory.link || post.categories.link ) + '/' + post._id`
                                :key="post._id"
                            ).last-news-post
                            img(:src="post.picture" :alt="post.title").last-news-post__img
                            .last-news-post__bottom
                                h3.subtitle.last-news-post__title {{ post.title }}
                                time.subtitle.last-news-post__date {{ dateParse(post.date)}}
</template>
<script>

import { mapActions, mapGetters } from 'vuex';
import toDateString from '../jsModules/toDateString';
export default {
    computed: {
        ...mapGetters('СategoriesStore', ['categories']),
        ...mapGetters('PostsStore', ['posts']),
    },
    methods: {
        ...mapActions('PostsStore', ['getLastPosts']),
        // getting posts in this category
        postsByCategory(postCat) {
            return this.posts.filter(post => post.categories.title === postCat);
        },
        dateParse(date) {
            return toDateString(date);
        }
    },
    created() {
        this.getLastPosts(5);
    },
}
</script>
<style lang="sass" src="./style.sass" scoped></style>




