import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
//  Import modules
import СategoriesStore from './modules/categoriesStore';
import PostsStore from './modules/postsStore';
import UsersStore from './modules/usersStore';

export const store = new Vuex.Store({
    modules: {
        СategoriesStore,
        PostsStore,
        UsersStore,
    },
});



