const routes = [
    { path: '/', component: require('../components/homepage') },
    { path: '/sign-in', component: require('../components/signIn') },
    { path: '/admin', component: require('../components/admin/posts') },
    { path: '/admin/posts', component: require('../components/admin/posts') },
    { path: '/admin/categories', component: require('../components/admin/categories') },
    { path: '/:cat', component: require('../components/categories') },
    { path: '/:cat/:subcat', component: require('../components/categories') },
    { path: '/:cat/:subcat/:id', component: require('../components/post') },
    { path: '*', component: require('../components/404') },
];
export default routes;



