<template lang="pug">
	div
		adminHeader
		b-container
			b-row.posts-panel
				b-col(class="col-sm-6")
					small(v-if="mode === 'edit'") Вы редактируете запись
					small(v-else-if="mode === 'create'") Вы пишите новую запись
					b-form(enctype="multupart/form-data")
						b-form-group(
							label="Заголовок:"
							label-for="postTitle"
							description="Введите заголовок новости")
							b-form-input(
								id="postTitle"
								type="text"
								v-model="newPost.title"
								required
								placeholder="Заголовок")

						b-form-group(
							label="Постер:"
							label-for="postPoster"
							description="Добавьте постер новости")
							b-form-file(
								id="postPoster"
								v-model="newPost.picture"
								choose-label="Загрузить"
								accept=".jpg, .jpeg, .png"
								ref="poster"
								required)

						b-form-group(
							label="Укажите основную рубрику"
							label-for="category"
							description="Все рубрики")
							b-form-select(
								id="category"
								v-model="newPost.category" :options="categoriesOptions"
								required)

						b-form-group(
							label="Укажите подрубрику (необязательно)"
							label-for="category"
							:description="'Подрубрики ' + newPost.category.title"
							v-if="newPost.category")
						b-form-select(
							id="category"
							v-model="newPost.subcategory"
							:options="subcategoriesTitles(newPost.category)")
				b-col(class="col-sm-6")
					news-list(
						@choosePost="choosePost"
						@deletePost="deletePost"
						)
			b-row
				b-col
					b-form-group(
						label="Содержимое:"
						label-for="postTitle"
						description="Введите содержимое новости"
						)
						wysiwyg(v-model="newPost.HTML")

					b-form-group(
						label="Шорткат:"
						label-for="postShortcat"
						description="Добавьте шорткат (краткое описание)")
						b-form-textarea(
							v-model="newPost.shortcat"
							placeholder="Краткое описание..."
							:rows="4"
							:max-rows="4"
							:no-resize="true")
					b-button(type="submit" @click="submitPost") Опубликовать
			b-modal(ref="alertModal" hide-footer) {{ infoAlert }}
</template>

<script>
import cyrToLat from "../../jsModules/cyrToLat";
import { mapGetters, mapActions } from "vuex";
export default {
	data() {
		return {
			newPost: {
				title: "",
				picture: "",
				HTML: "",
				category: "",
				subcategory: "",
				shortcat: ""
			},
			mode: "create",
			infoAlert: "",
		};
	},
	computed: {
		...mapGetters("СategoriesStore", ["categories"]),
		...mapGetters("UsersStore", ["thisUser"]),
		categoriesOptions() {
			let resultOptions = [];
			if (this.categories) {
				this.categories.map(category => {
					resultOptions.push({ value: { ...category }, text: category.title });
				});
			}
			return resultOptions;
		}
	},
	methods: {
		...mapActions("PostsStore", [
			"addNewPost",
			"getAllPostsPreview",
			"editPost",
			"removePost"
		]),
		...mapActions("UsersStore", ["getThisUser"]),
		subcategoriesTitles(category) {
			let resultOptions = [{ value: "", text: "Без подрубрики" }];
			if (category.subcategories) {
				category.subcategories.map(subCategory => {
					resultOptions.push({
						value: { ...subCategory },
						text: subCategory.title
					});
				});
			}
			return resultOptions;
		},
		submitPost(e) {
			if (this.validatePost()) {
				const categories = JSON.stringify({
					title: this.newPost.category.title,
					link: cyrToLat(this.newPost.category.title),
					subcategory: {
						title: this.newPost.subcategory.title
							? this.newPost.subcategory.title
							: "",
						link: cyrToLat(this.newPost.subcategory.title)
					}
				});
				const author = JSON.stringify(this.thisUser);
				const formData = new FormData();
				formData.append("poster", this.newPost.picture);
				formData.append("title", this.newPost.title);
				formData.append("content", this.newPost.HTML);
				formData.append("categories", categories);
				formData.append("shortcat", this.newPost.shortcat);
				formData.append("date", Date.now());
				formData.append("author", author);
				// if we create new post
				if (this.mode === "create") {
					this.addNewPost(formData).then(() => {
						this.showAlert('Ok!', 'Запись успешно добавлена!');
						this.getAllPostsPreview();
					});
				} else if (this.mode === "edit") {
					// if we edit existed post
					this.editPost(formData).then(() => {
						this.showAlert('Ok!', 'Запись успешно отредактирована!');
						this.getAllPostsPreview();
						this.mode = "create";
					});
				}
				// reset form
				this.clearForm();
			}
		},
		choosePost(post) {
			console.log("choose: ", post);
			this.newPost = {
				...post,
				category: post.categories,
				HTML: post.content
			};
			this.mode = "edit";
		},
		deletePost(id) {
			console.log("remove: ", id);
			this.removePost(id).then(() => {
				this.getAllPostsPreview();
				this.mode = "create";
			});
		},
		clearForm() {
			this.newPost = {
				title: "",
				picture: "",
				HTML: "",
				category: "",
				subcategory: "",
				shortcat: ""
			};
			this.$refs.poster.reset();
		},
		validatePost() {
			let errMessage = '';
			const post = this.newPost;
			!post.title ? errMessage+= 'Введите заголовок новости!\n' : null;
			!post.picture ? errMessage+= 'Добавьте обложку новости!\n' : null;
			!post.HTML ? errMessage+= 'Введите содержимое новости!\n' : null;
			!post.category ? errMessage+= 'Укажите рубрику новости!\n' : null;
			!post.shortcat ? errMessage+= 'Добавьте шорткат новости!\n' : null;
			if (errMessage) {
				this.showAlert('Ошибка!', errMessage);
				return false;
			}
			return true;
		},
		showAlert(title, text) {
            this.infoAlert = text;
			this.$refs.alertModal.show();
			this.$refs.alertModal.title = title;
        }
	},
	components: {
		newsList: require("./newsList"),
		adminHeader: require("./../_admin-common/header")
	}
};
</script>

<style lang="sass" scoped>
.posts-panel
	padding: 40px 0
.editr
	overflow-x: hidden
</style>




