<template lang="pug">
    b-navbar(toggleable="md" type="dark" variant="info" tag="header").header
        b-navbar-toggle(target="nav_collapse")
        router-link(to="/")
            img(src="~img/logo.png").logo
        b-collapse(is-nav id="nav_collapse")
            b-navbar-nav.nav
                li(v-for="item in categories" :key="item._id" class="nav__item")
                    router-link(
                                :to="'/'+item.link"
                                class="nav__link"
                                active-class="nav__link_active"
                                ) {{item.title}}
                    ul.nav__sub-list(v-if="item.subcategories.length")
                            li(v-for="subitem in item.subcategories" :key="subitem._id" class="nav__sub-item")
                                router-link(
                                    :to="'/' + item.link + '/' + subitem.link"
                                    class="nav__sub-link"
                                    active-class="nav__sub-link_active"
                                    ) {{subitem.title}}
            b-navbar-nav(class="ml-auto")
                router-link(
                    to="/sign-in"
                    class="btn btn-secondary"
                    v-if="!thisUser"
                    ) Войти
                router-link(
                    to="/admin"
                    class="btn btn-secondary"
                    v-else
                    ) Добавить
                
</template>

<script>
import {  mapGetters, mapActions } from 'vuex';
export default {
    computed: {
        ...mapGetters('СategoriesStore', ['categories']),
        ...mapGetters('UsersStore', ['thisUser']),
    },
    methods: {
        ...mapActions('UsersStore', ['getThisUser']),
    },
    created() {
        this.getThisUser();
    }
}
</script>



