import axios from 'axios';
import categoriesApi from './categoriesApi';
export const Api = {
    // TODO Api
    categoriesApi,
    getPostsByCategory(cat) {
        return axios.get(`/api/posts/${cat}`)
            .then(response => response.data)
            .catch(e => { console.error(e); return []; });
    },
    getLastPostsInAllCategories(count) {
        return axios.get('/api/lastPosts')
            .then(response => { console.log(response.data); return response.data; })
            .catch(e => { console.error(e); return []; });
    },
    getPostById(id) {
        console.log(id);
        return axios.get(`/api/post/${id}`)
            .then(response => response.data)
            .catch(e => { console.error(e); return []; });
    },
    getAllPostsPreview() {
        return axios.get('/api/postsAll')
            .then(response => response.data)
            .catch(e => { console.error(e); return []; });
    },
    addNewPost(post) {
        console.log(post);
        return axios.post('/api/addNewPost', post)
            .then(response => { console.log(response); return response; })
            .catch(e => { console.error(e); });
    },
    editPost(post) {
        console.log(post);
        return axios.put('/api/editPost', post)
            .then(response => { console.log(response); return response; })
            .catch(e => { console.error(e); });
    },
    removePost(id) {
        console.log(id);
        return axios.delete(`/api/removePost/${id}`)
            .then(response => { console.log(response); return response; })
            .catch(e => { console.error(e); });
    },
};



