<template lang="pug">
    #app
        app-header(v-if="!isAdminPage")
        router-view
        app-footer(v-if="!isAdminPage")
</template>

<script>
import AppHeader from './components/_common/header';
import AppFooter from './components/_common/footer';
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'app',
    data() {
        return {
            isAdminPage: false,
        };
    },
    methods: {
        ...mapActions('СategoriesStore', ['getAllCategories']),
    },
    watch: {
        '$route'(to) {
            console.log(to.path);
            this.isAdminPage = (to.path.slice(0, 6) === '/admin');
        }
    },
    created() {
        this.getAllCategories();
        this.isAdminPage = (this.$router.currentRoute.path.slice(0, 6) === '/admin');
    },
    components: {
        AppHeader,
        AppFooter,
    }
};
</script>



