import { Api } from './../../Api/Api';
const CategoriesStore = {
    namespaced: true,
    state: {
        data: [],
    },
    getters: {
        posts: ({ data }) => (data),
    },
    mutations: {},
    actions: {
        getAllPostsPreview(context) {
            return Api.getAllPostsPreview()
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        getPostsByCategory(context, category) {
            return Api.getPostsByCategory(category)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        getLastPosts(context, count) {
            return Api.getLastPostsInAllCategories(count)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        getPostById(context, id) {
            console.log(id);
            return Api.getPostById(id)
                .then(response => {
                    context.state.data = response;
                })
                .catch(e => console.error(e));
        },
        addNewPost(context, post) {
            return Api.addNewPost(post)
                .then(response => { console.log(response); return; })
                .catch(e => console.error(e));
        },
        editPost(context, post) {
            return Api.editPost(post)
                .then(response => { console.log(response); return; })
                .catch(e => console.error(e));
        },
        removePost(context, id) {
            return Api.removePost(id)
                .then(response => { console.log(response); return; })
                .catch(e => console.error(e));
        },
    },
};
export default CategoriesStore;



